<!--login page -->
<template>
  <v-container>
    <v-row>
      <v-col>
        <h1 style="margin-bottom: 4vh; margin-left: 4vh">注册</h1>
        <!--          https://vuetifyjs.com/en/components/forms/  Forms: a TextBox definer -->
        <v-form ref="signUpForm" v-model="formValidity" style="width: 80%">

          <!--            browser -->
          <v-text-field
              label="学号/教工号"
              outlined
              prepend-icon="mdi-account-circle"
              :rules="AccountRules"
              v-model="account"
          />
          <v-text-field
              label="姓名"
              outlined
              prepend-icon="mdi-account-circle"
              :rules="AccountRules"
              v-model="name"
          />
          <!--                           mdi-eye-off 隐藏密码-->
          <!--                    <v-text-field-->
          <!--                            label="密码"-->
          <!--                    />-->
          <v-text-field label="密码"
                        outlined
                        v-model="password"
                        :rules="passwordRules"
                        required
                        :type="showPassword ? 'text' : 'password'"
                        prepend-icon="mdi-lock"
                        :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        @click:append="showPassword = !showPassword"
          ></v-text-field>
          <!--                    :rules="PasswordRules"-->
          <v-text-field
              label="确认密码"
              outlined
              v-model="confirmPassword"
              :rules="confirmPasswordRules.concat(passwordConfirmationRule)"
              :type="showPassword ? 'text' : 'password'"
              required
              prepend-icon="mdi-lock-check"
              :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
          ></v-text-field>
          <!--                    注意如果这里没有结束符号></v-text-field> 两者就起不到联动作用-->
          <!--                    https://github.com/vuetifyjs/vuetify/blob/master/packages/docs/src/examples/v-text-field/misc-password.vue-->
          <v-autocomplete
              outlined
              label="选择你的用户身份"
              prepend-icon="mdi-account"
              :items="identities"
              v-model="identity"

          ></v-autocomplete>

          <v-text-field
              label="班级"
              outlined
              prepend-icon="mdi-map"
              v-model="sclass"
          ></v-text-field>

          <!--        这个是啥？这个是分割线 -->
          <v-divider></v-divider>
          <v-text-field
              label="学校"
              outlined
              prepend-icon="mdi-school"
              v-model="school"
          ></v-text-field>
          <v-text-field
              label="省份"
              outlined
              prepend-icon="mdi-school"
              v-model="province"
          ></v-text-field>
          <v-text-field
              label="Email"
              outlined
              prepend-icon="mdi-email"
              type="email"
              v-model="email"
              :rules="emailRules"
              required
          ></v-text-field>
          <v-checkbox
              label="同意协议"
              v-model="agreeToTerms"
              :rules="agreeToTermsRules"
              required
          ></v-checkbox>

          <v-btn
              @click="regist"
              type="submit"
              color="primary"
              class="mr-4"
              :disabled="!formValidity"
          >
            提交
          </v-btn>
          <v-btn color="rgba(0,166,172,0.8)" class="mr-4 white--text" @click="validateForm"
          >格式验证
          </v-btn>
          <v-btn color="rgba(253,185,51,0.8)" class="white--text" @click="resetForm">重置</v-btn>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "SignupPage",
  // data() {
  //     return ({
  //
  //         password: "",
  //         confirmPassword: "",
  //         showPassword: false
  //     })
  // },
  data() {
    return (
        {
          //注册用户的信息
          account: '',
          name: '',
          password: '',
          identity: '',
          email: '',
          school: '',
          sclass: '',
          province: '',

          resback: '',

          agreeToTerms: false,
          agreeToTermsRules: [
            value =>
                !!value ||
                '您需要阅读并同意条款'
          ],
          AccountRules: [
            value =>
                !!value ||
                '您需要输入账号'
          ],
          confirmPassword: "",

          passwordRules: [v => !!v || "请输入密码"],
          confirmPasswordRules: [v => !!v || "请输入验证密码"],

          identities: ['学生', '教师'],
          identityRules: [
            value =>
                !!value ||
                '您需要阅读选择你的身份'
          ],

          emailRules: [
            value => !!value || '您需要输入邮箱',
            value => value.indexOf('@') !== 0 || 'Email 应该有用户名',
            value => value.includes('@') || 'Email 应当包含“@”字符',
            value =>
                value.indexOf('.') - value.indexOf('@') > 1 ||
                'Email 格式错误',
            value => value.includes('.') || 'Email 应包含“.”字符',
            value =>
                value.indexOf('.') <= value.length - 3 ||
                'Email 应当包含有效域名'
          ],
          formValidity: false,
          showPassword: true,
        }
    )
  },
  computed: {
    passwordConfirmationRule() {
      return () =>
          this.password === this.confirmPassword || "密码不一致";
    }
  },
  methods: {
    //  https://vuetifyjs.com/en/components/forms/#rules
    resetForm() {
      this.$refs.signUpForm.reset()
    },
    validateForm() {
      this.$refs.signUpForm.validate()
    },
    regist: function () {

      var that = this;
      if (this.identity === '学生') {
        axios({
          url: "http://127.0.0.1:9090/student/register",
          method: 'post',
          params: {
            id: that.account,
            name: that.name,
            password: that.password,
            classid: that.sclass,
          }
        })
            //在 then的内部不能使用Vue的实例化的this, 因为在内部 this 没有被绑定。
            .then((response) => {
              // 用ES6箭头函数，箭头方法可以和父方法共享变量
              // 在请求axios外面定义一下 var that=this
              if (response.data.code === "1"){
                alert("注册失败");
                location.reload();
              }else {
                that.resback = response;
                alert("注册成功！欢迎同学" + that.name);
              }
            })
            .catch(function (err) {
              alert("网络问题，请稍后重试，错误代码：" + err.data.code);
            })
      } else if (this.identity === '教师') {
        axios({
          url: "http://127.0.0.1:9090/teacher/register",
          method: 'post',
          params: {
            id: that.account,
            password: that.password,
            name: that.name,
            classid: that.sclass,
          }
        })
            //在 then的内部不能使用Vue的实例化的this, 因为在内部 this 没有被绑定。
            .then((response) => {
              // 用ES6箭头函数，箭头方法可以和父方法共享变量
              // 在请求axios外面定义一下 var that=this
              if (response.data.code === "1"){
                alert("注册失败");
                location.reload();
              }else {
                that.resback = response;
                alert("注册成功！欢迎老师" + that.name);
              }
            })
            .catch(function (err) {
              alert("网络问题，请稍后重试，错误代码：" + err.data.code);
            })
      }
    }
  },

}

</script>
